
import { Auth, API } from 'aws-amplify'


export const sanboxPush = async (data, callback) => {

    // example: 
    // data  = {
    //     method: 'POST',
    //     body: {
    //         path: '/accounts/000-000',
    //         payload : { ... },    // optional in case of 'GET' 
    //         claim: lemonWayAccountId,  //optional only in case of account creation
    //         id: formateurId
    //     }
    // }

    let user = null
    try {
        user = await Auth.currentAuthenticatedUser()

    }
    catch (err) {
        console.log(err)
    }



    const token = user ? user.signInUserSession.idToken.jwtToken : null





    const requestInfo = {
        headers: {
            Authorization: token
        },
        body: data
    }



    const initialTrial = 0


    // name of the API is misleading. It is a general API call not createIndividualAccount

    const requestTry = (trial) => {

        if (trial < 10)

        API.put('sandboxNew', '/createIndividualAccount ', requestInfo)
            .then(response => {
                console.log('@TX  SUCCESS')
                console.log(response)
                callback(response)

            })
            .catch(err => {
                trial += 1
                console.log('@ERROR TRIAL: ' + trial)
                requestTry(trial)
                console.log(err)
            })

    }


    console.log('@INITIATING TX WITH LW')
    console.log(requestInfo.body.payload)
    Object.keys(requestInfo.body.payload).forEach(key=>{
        console.log(key, requestInfo.body.payload[key])
    })
    requestTry(initialTrial)



        return 0

}