import React, { useContext, useState, useEffect } from "react"

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import { grey, red, yellow } from '@mui/material/colors'



import SearchIcon from '@mui/icons-material/Search'

import Logo from '../assets/logo_pekoia_horizontal.svg'
import searchBarStyle, { sxStyle, BootstrapInput } from './style/header'
import { useBreakpoint } from 'gatsby-plugin-breakpoints'
import { navigate } from "gatsby-link"
import LoginModalMenu from './loginModal'


// ADD BACK WHEN NEEDED
//
//import CookiesModal from '../workflows/cookiesModal/main'
//import cookies from 'js-cookie'


import { AuthContext } from '../context/general'
import TvIcon from '@mui/icons-material/Tv'


const ReactLazyPreload = importStatement => {
  const Component = React.lazy(importStatement);
  Component.preload = importStatement;
  return Component;
}

const LoginModal = ReactLazyPreload(() =>
  import('../workflows/newLoginModal/main')
)




const Header = (props) => {

  const { ssrTest, setSsrTest } = props

  const classes = searchBarStyle(props)
  const breakpoints = useBreakpoint()
  const [count, setCount] = useState(0)

  const mobileOnly = breakpoints.xs
  const { authState } = useContext(AuthContext)
  const transactionsData = authState.transactions
  const [showTV, setShowTV] = useState(false)

  const isSSR = typeof window === "undefined"
  const [isMounted, setIsMounted] = useState(false)




  useEffect(() => {
    setIsMounted(true)
    LoginModal.preload()


  }, [])



  // ADD BACK WHEN NEEDED
  //
  // React.useEffect(() => {

  //   let PekoiaConsent = cookies.get('PekoiaConsent')

  //   if (PekoiaConsent) {

  //     const cookieData = JSON.parse(PekoiaConsent)
  //     authActions.setCookies(cookieData)

  //   }
  //   else
  //     authActions.toggleCookiesModal()


  //   return () => {
  //     PekoiaConsent = null;
  //   }



  // }, [])



  useEffect(() => {





    const now = Date.now() / 1000



    const ids = transactionsData
      .filter(transaction => transaction?.id)
      .map(transaction => transaction.id)

    const NoDoubleIds = [...new Set(ids)]

    const newTransactions = NoDoubleIds.map(id => transactionsData.find(transaction => transaction?.id === id))

    const futureTransactions = newTransactions
      .sort((a, b) => a.date - b.date)
      .filter(transaction => transaction.date + transaction.duree * 60 > now)



    if (!showTV && futureTransactions.length && (futureTransactions[0].date - now) > -3600)
      setShowTV(futureTransactions[0])




    const timer = setTimeout(_ => {


      setCount((count + 1) % 10)


    }, 10000)

    return () => {
      clearTimeout(timer)
    }



  }, [count, transactionsData])

  // const LoginModal = React.lazy(() => import('../workflows/newLoginModal/main'))









  return (


    <Box display="flex" flexDirection="column" >
      <LoginModalMenu />
      {/* ADD BACK WHEN NEEDED */}
      {/* <CookiesModal /> */}
      {!isSSR && <React.Suspense fallback={<div></div>}>
        <LoginModal />
      </React.Suspense>}




      {!isSSR && <Box sx={sxStyle.headerContainer} >


        <Box sx={sxStyle.logoContainer} flex={1}>

          <Box marginTop="30px" display="flex" flexDirection="row" alignContent="center" alignItems="center">
            <LogoZone ssrTest={ssrTest} setSsrTest={setSsrTest} />
            {count <= 9 && showTV && <Box onClick={() => { navigate('/hub?inscription=' + showTV.id) }} style={{ cursor: 'pointer', height: '90%', marginLeft: "20px" }}>
              <Box width='150px' display="flex" flexDirection='column' alignContent='center' alignItems='center' justifyContent='center' justifyItems='center'>
                <TvIcon style={{ marginTop: "12px", color: yellow[200] }} /> <Typography style={{ fontSize: '0.3rem', color: yellow[200] }}>
                  prochainement
                </Typography>
              </Box>
            </Box>}

          </Box>

        </Box>
        <Box display="flex" flexDirection="row" justifyItems="stretch">
          <SearchZoneMobile />

          {!mobileOnly && <Box sx={sxStyle.aboutUs} width='600px' >
            <AboutUsZone />
            <SearchZone />
          </Box>}
        </Box>





      </Box>}

    </Box>
  )
}


export default Header



const LogoZone = (props) => {

  const { ssrTest, setSsrTest } = props


  const handleClick = () => {

    //setSsrTest(!ssrTest)

    navigate('/')

  }



  const classes = searchBarStyle()

  return (
    <Box onClick={handleClick} style={{ cursor: 'pointer' }} >

      <Logo style={{ height: '40px', fill: "white" }} />
    </Box>


  )
}

const NavigationZone = () => {



  return (
    <Box display="flex" flexDirection="row" alignContent="center" alignItems="center" height="100%">
      {/* <DisplaySingleItem label="événements" />
      <DisplaySingleItem label="intervenants" />
      <DisplaySingleItem label="outils" /> */}
    </Box>
  )
}

const AboutUsZone = () => {
  const classes = searchBarStyle()
  const fontStyle = { textTransform: 'capitalize', fontSize: '0.7rem', fontWeight: 600 }
  const DisplaySingleItem = ({ label, slug }) => {
    return (<Box padding="0 20px 0 20px" width='110px' onClick={() => navigate(slug)} style={{ cursor: 'pointer' }}>
      <Typography style={fontStyle}>
        {label}
      </Typography>
    </Box>)
  }

  return (
    <Box sx={sxStyle.aboutUsZone}>
      <DisplaySingleItem label="à propos" slug="/about" />

      <DisplaySingleItem label="Actualité" />
    </Box>
  )
}

const SearchZoneMobile = () => {
  const classes = searchBarStyle()

  return (
    <Box sx={sxStyle.searchZoneMobile} >
      <SearchIcon sx={{ fontSize: '3rem', marginRight: "10px", paddingTop: '10px' }} />


    </Box>)
}

const SearchZone = () => {

  const classes = searchBarStyle()
  return (
    <Box display="flex" flexDirection="row" width="auto" alignItems="center" height="100%"  width="280px">


      <Box sx={sxStyle.search} >



        <BootstrapInput
          placeholder="RECHERCHER"
          inputProps={{ 'aria-label': 'search' }}
        />




      </Box>
      <Box sx={sxStyle.searchIcon} position="relative" marginLeft="65px">
        <SearchIcon sx={{ fontSize: '1.9rem' }} />
      </Box>

    </Box>
  )
}
