import React, { useEffect, useContext } from 'react'
import makeStyles from '@mui/styles/makeStyles'
import { Typography, Box } from '@mui/material'
import { navigate } from 'gatsby-link'
import TvIcon from '@mui/icons-material/Tv'

import { AuthContext } from "../../context/general"



const DisplayReplays = () => {

    const { authState, authActions } = useContext(AuthContext)

    const transactionsData = authState.transactions
    const  replaysData = authState.replays

    const classes = useStyles()

   

    const now = Date.now() / 1000


    return (<Box padding="1px" >
        {replaysData.map((transaction, index) => {
            return (<Box key={index} display="flex" flexDirection="row" alignContent="center" alignItems="center" className={classes.itemList} onClick={() => {
                    navigate('/video?eventKey=' + transaction.id)
               
            }}>
                <Box margin="7.5px" padding="1px" height="40px" width="40px" >
                    <img style={{ width: '100%', height: '100%', objectFit: 'cover' }} src={`https://pekoia.com/static/L${transaction.imageKey}-300-${transaction.imageETag300}.jpg`} alt={transaction.title1} />
                </Box>
                <Box display="flex" flexDirection="column" position="relative" bottom="2px" justifyContent="center">
                    <Box  >
                        <Typography style={{ fontSize: '0.7rem', lineHeight: '0.7rem' }}>
                            {transaction.title1}
                        </Typography>
                    </Box>
                    <Box position="relative" top="5px">
                        <Typography style={{ fontSize: '0.5rem' }}>
                            {/* {displayTime(transaction.date - now)} */}
                        </Typography>
                    </Box>

                </Box >
                {/* {((transaction.date - now) < remainingTime) && <Box style={{ cursor: 'pointer' }} position="absolute" right={10} onClick={() => {
                    navigate('/hub?inscription=' + transaction.id)

                }}>
                    <TvIcon style={{ height: "20px", width: "20px" }} />
                </Box>} */}



            </Box>)
        }

        )}
    </Box>)
}

export default DisplayReplays

const useStyles = makeStyles({

    notificationItem: {
        cursor: 'pointer',
        '&:hover': {
            backgroundColor: 'black'
        }

    },

    itemList: {

        cursor: 'pointer',


        '&:hover': {
            backgroundColor: 'black'
        }



    },

    small: {
        width: '30px',
        height: '30px',
    },
    logo: {
        width: '30px',
        height: '30px',
    }
});